axios.get(url).then(response => {
    const potions = response.data
    console.log(potions)
})

let potionName = null

const lightboxUI = (props) => {
    return <div id="light" className="white_content">
            <button onClick={props.closeLb}>&#10006;</button>
            {/* <img src={require(`../../assets/images/products/${fixedImgName.charAt(0).toLowerCase() + fixedImgName.slice(1)}.png`)} alt=""/> */}
            <aside className="details">
                <strong><p>{potionName}</p></strong>

            </aside>
        </div>
        
}

export default lightboxUI