import React from 'react'

const potion = (props) => {
    let imgName = props.name
    let fixedImgName = imgName.split(' ').join('')
    return (<div className="Potion" onClick={props.openLb}>
        <img src = {require(`../../assets/images/products/${fixedImgName.charAt(0).toLowerCase() + fixedImgName.slice(1)}.png`)} alt={`${imgName}`}/>
        <p><strong>{imgName}</strong> - <span className="red-text">{props.price}</span></p>
    </div>)
}

export default potion