import React from 'react'

const headerUI = (props) => {
    return <header className="header-container header-display">
            <i className="fas fa-bars laptop-hidden pointer" onClick={props.openMenu}></i>
            <h1 className="fontweight-bold red-text fontHeight-small fontSize-merlin">
                MERLIN'S <span className="fontweigth-thin">POTIONS</span>
            <br/>
            <span className="purple-text">fine potions since 1026</span>            
            </h1>
            <input type="text" placeholder="Search our stock" className="input-display input-header mobile-hidden" />
            <p className="red-text fontFamily-TNR fontSize-big fontStyle-italic mobile-hidden">Free shipping<br/><span className="fontSize-small">on orders over $50</span></p>
            <div className="cauldron-bag">
                <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRvGD1aUcLJ8JH_qoUGuQFh9gFxKOOjoPP7gkOK9ERcPja31i40" alt="cauldron"/>
                <span className="fontSize-big lightGray-text">BAG: <span className="fontweight-bold red-text">1</span></span>
            </div>
        </header>
}

export default headerUI