import React from 'react'
import Potion from './Potion'

const Potions = (props) => props.potionsState.map((potion) => {
    return <Potion
            openLb={props.openLightbox}
            name={potion.name} 
            price={potion.price} 
            key={potion.id}/>
    })



export default Potions