import React from 'react'

const mobileNavUI = (props) => {
    return <aside id="mySidenav" className="sidenav">
            <div className="menu-header">
                <i className="closebtn fas fa-times" onClick={props.closeMenu}></i>
                <input type="text" placeholder="Search our stock" className="input-display input-header" />
            </div>
                <a href="#">Potions</a>
                <a href="#">Ingredients</a> 
                <a href="#">Books</a>
                <a href="#">Supplies</a>
                <a href="#">Charm</a>
                <a href="#" id="hover-fixed">Cleareance!</a>
            <div className="user-mobile">
                <a href="#">Sing In</a>
                <span className="lightGray-text">or</span>
                <a href="#">Sing Up</a>
            </div>
            </aside>

    
}

export default mobileNavUI