import '../../../node_modules/normalize.css/normalize.css'
import React, { Component } from 'react'
import Header from '../template/Header'
import Nav from '../template/Nav'
import Footer from '../template/Footer'
import Potions from '../template/Potions'
import MobileMenu from '../template/MobileMenu'
import Lightbox from '../template/Lightbox'
import '../../styles/main.scss'

class App extends Component {
        state = {
        potions: [
        { id: '1', name: 'Aging Potion', price: '$29.99' },
        { id: '2', name: 'Bulgeye Potion', price: '$19.99' },
        { id: '3', name: 'Dragon Tonic', price: '$64.99' },
        { id: '4', name: 'Love Potion', price: '$29.99' },
        { id: '5', name: 'Polyjuice Potion', price: '$99.99' },
        { id: '6', name: 'Sleeping Draught', price: '$14.99' }]
        }

        openNav() {
            document.getElementById("mySidenav").style.width = "100vw";
        }
        closeNav() {
            document.getElementById("mySidenav").style.width = "0";
        }
        
        lightBoxOn() {
            document.getElementById('light').style.display='flex';
            document.getElementById('fade').style.display='block';
        }
        lightBoxOff() {
            document.getElementById('light').style.display='none'
            document.getElementById('fade').style.display='none'
        }


    render() {
        return (<React.Fragment>
                <Lightbox closeLb={this.lightBoxOff}/>
                <Header openMenu={this.openNav} />
                <Nav/>
                <MobileMenu closeMenu={this.closeNav}/>
                <h1 className="potions-container fontFamily-TNR">
                Potions
                <div className="potions-display">     
                <Potions potionsState={this.state.potions} openLightbox={this.lightBoxOn}/>
                </div>
                </h1>
                <Footer/>
                <div id="fade" className="black_overlay"></div>
        </React.Fragment>)
    }
}

export default App
